import { useState, useEffect } from 'react';
import Head from 'next/head';
import { SearchBar } from '../components/SearchBar';
import { AnimeCard } from '../components/AnimeCard';
import { api } from '../services/api';

interface Anime {
  id: string;
  title: string;
  genre: string;
}

export default function Home() {
  const [animes, setAnimes] = useState<Anime[]>([]);
  const [loading, setLoading] = useState(true);

  // Carrega os animes ao iniciar
  useEffect(() => {
    const loadAnimes = async () => {
      try {
        const response = await api.getAnimes();
        setAnimes(response.data);
      } catch (error) {
        console.error('Erro ao carregar animes:', error);
      } finally {
        setLoading(false);
      }
    };
    loadAnimes();
  }, []);

  const handleSearch = (query: string) => {
    console.log('Buscar:', query); // Implementaremos depois
  };

  const handleDelete = async (id: string) => {
    try {
      await api.deleteAnime(id);
      setAnimes(animes.filter(anime => anime.id !== id));
    } catch (error) {
      console.error('Erro ao deletar:', error);
    }
  };

  if (loading) return <div className="text-center mt-8">Carregando...</div>;

  return (
    <>
      <Head>
        <title>Catálogo de Animes</title>
      </Head>

      <div className="min-h-screen bg-[#F8F9FA] p-6">
        <div className="max-w-4xl mx-auto">
          <h1 className="text-4xl font-bold text-[#EE0000] mb-8 text-center">
            Meu Catálogo
          </h1>
          
          <SearchBar onSearch={handleSearch} />

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-6">
            {animes.map((anime) => (
              <AnimeCard
                key={anime.id}
                id={anime.id}
                title={anime.title}
                genre={anime.genre}
                onEdit={(id) => console.log('Editar:', id)}
                onDelete={handleDelete}
              />
            ))}
          </div>
        </div>
      </div>
    </>
  );
}