import React from 'react';

interface AnimeCardProps {
  id: string;
  title: string;
  genre: string;
  onEdit: (id: string) => void;
  onDelete: (id: string) => void;
}

export const AnimeCard: React.FC<AnimeCardProps> = ({ id, title, genre, onEdit, onDelete }) => {
  return (
    <div className="bg-white p-4 rounded-lg shadow-sm hover:shadow-md transition border-l-4 border-[#EE0000]">
      <h2 className="text-xl font-semibold text-gray-800">{title}</h2>
      <p className="text-[#86A8E7] mb-2">{genre}</p>
      <div className="flex justify-end space-x-2">
        <button 
          onClick={() => onEdit(id)} 
          className="text-[#86A8E7] hover:text-blue-600 px-2 py-1 rounded"
        >
          Editar
        </button>
        <button 
          onClick={() => onDelete(id)} 
          className="text-[#EE0000] hover:text-red-600 px-2 py-1 rounded"
        >
          Excluir
        </button>
      </div>
    </div>
  );
};